.ninja-squad-container .more-button {
  margin: 1em auto;
  display: block;
  padding: 0.5em 1em;
  border: none;
  border-radius: 0.3em;
  background-color: #002B55;
  color: #80C565;
}

.ninja-squad-container .more-button.hidden {
  display: none;
}

.ninja-squad {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}