.toolbar input[type="checkbox"] {
  display: none;
}

.toolbar input + label {
  padding: 0.3em;
  margin: 0.3em;
  display: inline-block;
  border: 1px solid #80C565;
  border-radius: 0.2em;
  text-transform: capitalize;
}

.toolbar input:checked + label {
  background: white;
  color: black;
  /*font-weight: bold;*/
  border-color: white;
}

.toolbar form {
  display: flex;
  flex-direction: column;
}

.toolbar .button-bar {
  display: flex;
  flex-wrap: wrap;
  padding-top: 1em;
  justify-content: center;
}

.toolbar .name-input {
  width: 10em;
  margin: 0 auto;
  border: none;
  border-bottom: 2px solid #80C565;
  background: none;
  color: white;
}

.toolbar .name-input:focus {
  outline: none;
}